import {defineStore} from "pinia";

export const useModelStore = defineStore(
    'model',
    {
        state: () => ({
            
            model:{},
        }), 
        persist: {
            storage: sessionStorage,//别动
            key:'currentModel'
        },
        actions: {
            getModel(): any {
                return this.model
            },
            setModel(item:any) {
                    this.model=item
            }
        }
    })
